<template>
  <div class="block">
    <el-row>
      <span class="demonstration"></span>
      <el-cascader placeholder="" v-model="value" :options="options" @change="handleChange" style="width:300px"></el-cascader>
      <span class="demonstration" ></span>
      <el-input placeholder="" v-model="input" clearable style="width:300px"></el-input>
      <el-button type="warning" size="small" @click="inputAddress"> </el-button>
    </el-row>
  </div>
</template>

<script>

  export default {
    name: "Address",
    data() {
      return {
        selectedOptions: [],//
        options: this.GLOBAL.options,   //
        input: '',
        value: ''
      }
    },
    methods:{
        handleChange(value) {
          console.log(value);
        },
        inputAddress(){
          this.$message({
            type: 'success',
            message: '!'
          });
        }
    }

  }

</script>

<style scoped>
  .block{
    float:left;
    margin-left:250px;
    margin-bottom:40px;
  }
  .demonstration{
    margin-left:40px;
    margin-right:10px;
  }

</style>



