<template>
  <div>

  <div>
    <!--        -->
    <el-dropdown>
      <span class="el-dropdown-link">
        {{sortType}}<i class="el-icon-arrow-down el-icon--right"></i>
      </span>
      <el-dropdown-menu slot="dropdown">
<!--        <el-dropdown-item></el-dropdown-item>-->
        <el-dropdown-item @click.native="sortByPrice"></el-dropdown-item>
        <el-dropdown-item @click.native="sortByCnt"></el-dropdown-item>
      </el-dropdown-menu>
    </el-dropdown>

    <el-radio-group v-model="radio" fill="#FFCD56" >
      <el-radio-button label="" class="radio" @click.native="dataInPage = goodsList"></el-radio-button>
      <el-radio-button label="" @click.native="dataInPage = goodsList.filter(data => ('' === data.type))"></el-radio-button>
      <el-radio-button label="" @click.native="dataInPage = goodsList.filter(data => ('' === data.type))"></el-radio-button>
      <el-radio-button label="" @click.native="dataInPage = goodsList.filter(data => ('' === data.type))"></el-radio-button>
    </el-radio-group>
    <el-autocomplete
      class="input inline-input"
      :fetch-suggestions="querySearch"
      :trigger-on-focus="false"
      @select="handleSelect"
      placeholder="" v-model="input" style="width:300px">
    </el-autocomplete>
    <el-button icon="el-icon-search" @click="searchGoods" class="search" size="small"></el-button>
    <!--    -->
    <el-popover
      placement="bottom" width="400"
      trigger="manual" v-model="visible">
      <!-- TODO -->
      <CartDialog v-if="visible" :sid="SID" ref="cart"
                  @Buy="sonBuy" @Add="sonAddCnt" @Sub="sonSubCnt" @Delete="sonDelete" @Change="sonChange">
      </CartDialog>
      <el-button slot="reference" size="small" icon="el-icon-shopping-cart-full" type="success"
                 @click="visible = !visible">
        
      </el-button>
    </el-popover>
  </div>


  <!--      main-->
  <el-col :span="5" v-for="(o, index) in dataInPage" :key="o.id" :offset="index > 0 ? 3 : 0" class="box-card">
    <el-card :body-style="{ padding: '0px' }" shadow="hover" class="card">
      <el-container>
        <el-aside width="180px">
          <div class="header-style">
            <img v-if="o.pictureUrl === '' || o.pictureUrl === null" :src="require('@/assets/img/default.jpg')" class="food-image">
            <img v-else :src="o.pictureUrl" class="food-image">
          </div>
        </el-aside>
        <el-container>
          <el-header>
            <span style="float: left; padding: 18px 0">{{o.name}}</span>
            <el-button style="float: right; padding: 20px 0" type="text"
                       @click="showBuyCard(o.id, index)"></el-button>
          </el-header>
          <el-main >
            <span style="float: left;color:#FFCD56; font-weight: bolder;">{{o.price}}</span>
            <el-button type="danger"
                       v-show="getMapCnt(o.id) > 0"
                       @click="showBuyCard(o.id, index)"
                       round size="mini" style="float: right;">{{getMapCnt(o.id)}}</el-button>
          </el-main>
          <el-footer></el-footer>

        </el-container>
      </el-container>
    </el-card>
  </el-col>

  <!--            -->
  <el-dialog :visible.sync="dialogFormVisible" center>
    <!---->
    <GoodsCard :value="goodsList[nowEditIndex]"></GoodsCard>
    <el-divider></el-divider>
    <!---->

    <!---->
    <div slot="footer" class="dialog-footer">
      <!--        -->
      <el-button size="mini" v-if="selectedNum === 0" type="warning" @click="addToCart(goodsList[nowEditIndex].id)">+</el-button>
      <template v-else>
        <el-button type="danger" icon="el-icon-minus" @click="subCnt(goodsList[nowEditIndex].id)" circle size="mini"></el-button>
        <el-tag type="info" style="width:100px;">{{selectedNum}}</el-tag>
        <el-button type="success" icon="el-icon-plus" @click="addCnt(goodsList[nowEditIndex].id)"
                   circle size="mini" style="background-color: #EBB563; border-color: #EBB563"></el-button>
        <el-button type="primary"  v-show="selectedNum > 0" @click="submit"
                   size="mini" class="dia-btn"></el-button>
        <el-button type="primary"  v-show="selectedNum > 0" @click="buy(goodsList[nowEditIndex].id, new Date().Format('yyyy-MM-dd hh:mm:ss'))"
                   size="mini" class="dia-btn"></el-button>
        <el-button size="mini" style="float:right" type="success" @click="lookCart"><i class="el-icon-shopping-cart-1"> </i></el-button>
      </template>
    </div>
  </el-dialog>

  </div>
</template>

<script>
  import GoodsCard from '@/components/card/GoodsCard'
  import CartDialog from '@/components/CartDialog'

  export default {
    name: "Menu",
    components: {GoodsCard, CartDialog},
    props: ['sid'],
    data() {
      return {
        SID: 1,

        // 
        formLabelWidth: '70px',
        dialogFormVisible: false,
        nowEditIndex: 0,
        selectedNum: 0,
        selectedNumMap: {},

        // 
        radio: '',
        input: '',

        // 
        sortType: '',
        dataInPage: [],
        goodsList: [
          {
            buyCount: 331,
            desc: "",
            id: 3,
            name: "",
            value: '',
            pictureUrl: "http://localhost:8080/takeout/upload/201907011702352.jpg",
            praise: 0,
            price: 200,
            sid: 1,
            stock: 1,
            type: ""
          }
        ],
        visible: false
      }
    },
    created() {
      // console.log(this.$route.params)
      this.SID = this.sid
      this.refreshPage()
    },
    watch: {
      'sid' () {
        // console.log(this.$route.params)
        this.SID = this.sid
        this.refreshPage()
      }
    },
    methods: {
      // @[]
      cardMessage (val) {
        this.card = val;
      },
      // 
      searchGoods () {
        // console.log(this.radio)
        this.dataInPage = this.goodsList.filter(data =>
          (this.radio === '' || data.type === this.radio) &&
          (!this.input || data.name.toLowerCase().includes(this.input.toLowerCase())))
      },

      //  SID  
      refreshPage() {
        // FIXME: , 
        this.$get('/consumer/getShoppingCartByCid', {
          CID: this.$db.get('USER_ID')
        }).then(res => {
          let data = res.data.data
          for (let i = 0, len = data.length; i < len; i++) {
            this.selectedNumMap[data[i].gid] = data[i].num // 
          }
        }).catch(err => console.log(err))

        // 
        this.$get('/shop/getAllGoodsBySid', {
          SID: this.SID
        }).then(res => {
          // console.log(res.data)
          this.goodsList = res.data.data;
          for (let i = 0, len = this.goodsList.length; i < len; i++) {
            this.goodsList[i].value = this.goodsList[i].name
          }
          this.dataInPage = this.goodsList;
          this.radio = ""
        }).catch(err => console.log(err))
      },
      //  
      showBuyCard(GID, index) {
        // 
        index = -1
        for (let i = 0, len = this.goodsList.length; i < len; i++) {
          if (this.goodsList[i].id === GID) {
            index = i
            break
          }
        }
        if (index === -1) {
          this.$message({
            type: 'info',
            message: '.'
          })
          return
        }
        this.nowEditIndex = index
        // 
        this.$get('/consumer/getShoppingCartByCid', {
          CID: this.$db.get('USER_ID')
        }).then(res => {
          let data = res.data.data
          for (let i = 0, len = data.length; i < len; i++) {
            if (data[i].gid === GID) {
              // 
              this.selectedNum = data[i].num;
              this.selectedNumMap[GID] = data[i].num
              return
            }
          }
          // 
          this.selectedNum = 0
          this.selectedNumMap[GID] = 0
        }).catch(err => console.log(err))
        // 
        this.dialogFormVisible = true
        this.visible = false
      },
      // 
      // 1 
      buy(GID, date) {
        this.$confirm('?', '', {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
        }).then(() => {
          // 
          this.$get('/ShoppingCart/confirmItemWithDate', {
            CID: this.$db.get('USER_ID'),
            GID: GID,
            now: date
          }).then(res => {
            this.$message({
              type: 'info',
              message: ''
            })
            //  0
            this.selectedNum = 0
            this.selectedNumMap[GID] = 0
            // FIXME: 
            this.$buyNotification.send(JSON.stringify({SID: this.SID}))
          }).catch(err => console.log(err))
        }).catch(() => {
          this.$message({
            type: 'info',
            message: ''
          })
        })
      },
      // 2 
      lookCart() {
        this.dialogFormVisible = false
        this.visible = true
      },
      // 3 
      addToCart(GID) {
        // 
        if (this.goodsList[this.nowEditIndex].stock === 0) {
          this.$message({
            type: 'info',
            message: '!'
          })
          return
        }
        //
        this.$get('/ShoppingCart/add', {
          CID: this.$db.get('USER_ID'),
          GID: GID
        }).then(res => {
          // 1
          this.selectedNum = 1
          this.selectedNumMap[GID] = 1
        }).catch(err => console.log(err))
      },
      // 
      addCnt(GID) {
        // 
        if (this.selectedNum >= this.goodsList[this.nowEditIndex].stock) {
          this.$message({
            type: 'info',
            message: '!'
          })
          return
        }
        this.$get('/ShoppingCart/numAdd', {
          CID: this.$db.get('USER_ID'),
          GID: GID
        }).then(res => {
          this.selectedNum += 1
          this.selectedNumMap[GID] += 1
        }).catch(err => console.log(err))
      },
      subCnt(GID) {
        //  0 
        if (this.selectedNum === 1) {
          this.$get('/ShoppingCart/deleteItem', {
            CID: this.$db.get('USER_ID'),
            GID: GID
          }).then(res => {
            this.selectedNum = 0
            this.selectedNumMap[GID] = 0
          }).catch(err => console.log(err))
          return
        }
        //
        this.$get('/ShoppingCart/numSub', {
          CID: this.$db.get('USER_ID'),
          GID: GID
        }).then(res => {
          this.selectedNum -= 1
          this.selectedNumMap[GID] -= 1
        }).catch(err => console.log(err))
      },
      //
      submit(){
        this.dialogFormVisible=false;
      },
      // 
      querySearch(queryString, cb) {
        let goods = this.goodsList;
        let results = queryString ? goods.filter(this.createFilter(queryString)) : goods;
        //  callback 
        cb(results);
      },
      createFilter(queryString) {
        return (good) => {
          return (good.name.toLowerCase().indexOf(queryString.toLowerCase()) !== -1);
        };
      },
      handleSelect(item) {
        console.log(item);
      },

      // 
      sortByPrice() {
        this.sortType = ''
        this.dataInPage = this.dataInPage.sort((x, y) => x.price - y.price)
      },
      sortByCnt() {
        this.sortType = ''
        this.dataInPage = this.dataInPage.sort((x, y) => x.buyCount - y.buyCount)
      },

      // 
      sonBuy(items) {
        // console.log("buy")
        for (let item of items) {
          // 
          this.selectedNumMap[item.gid] = 0
        }
        // 
        this.visible = false
        // FIXME: 
        this.$buyNotification.send(JSON.stringify({SID: this.SID}))
      },
      sonAddCnt(GID) {
        this.selectedNumMap[GID] += 1
      },
      sonSubCnt(GID) {
        this.selectedNumMap[GID] -= 1
      },
      sonChange(row) {
        this.$set(this.selectedNumMap, row.gid, row.num)
        // console.log(this.selectedNumMap)
        // console.log("change")
        this.visible = false
        this.visible = true
      },
      sonDelete(items) {
        // console.log("delete")
        for (let item of items) {
          this.selectedNumMap[item.gid] = 0
        }
        this.visible = false
        this.visible = true
      },

      // sb
      getMapCnt(gid) {
        return this.selectedNumMap[gid]
      }
    },
  }
</script>

<style scoped>
  .el-row {
    margin-bottom: 20px;
  }
  .bg-light {
    background: 	#FDF2D4;
  }
  .grid-content {
    border-radius: 4px;
    min-height: 80px;
  }
  .bg-white{
    background: #FDFDFD;
  }
  .shop-name{
    margin:10px 20px 10px;
    padding:0;
    background-color:#FDF2D4;
    font-weight: bold;
    float:left;
  }
  .header{
    margin:0;
    padding:0;
    background-color:#FDF2D4;
    font-weight: bold;
    height:25px;
    border-radius: 4px;
  }
  .message{
    height:75px;
    background-color: #F5F5F5;
  }
  .shop-image{
    width: 150px;
    height:80px;
    margin:10px;
    display: block;
  }
  .shop-info{
    float:left;
    margin-right:40px;
  }
  .food-image {
    width: 150px;
    height:100px;
    margin:10px;
    display: block;
  }
  .box-card {
    width: 400px;
    height: 150px;
    margin:10px auto 10px 70px;
  }
  .card{
    height:130px;
  }
  .card:hover{
    background-color:#F5F5F5;
  }
  .input{
    width:250px;
    margin:auto auto auto 25px;
  }
  .el-pagination.is-background .el-pager li:not(.disabled).active{
    background-color: #FFCD56 !important;
  }
  .el-pagination.is-background .el-pager li:hover{
    color: #FFCD56 !important;
  }
  .input{
    border-color: #FFCD56;
  }
  .search{
    margin:10px;
  }
  .search:hover{
    color:#FFCD56;
    border-color: #FFCD56;
    background-color: rgba(255,205,86,0.1);
  }
  .search:focus{
    color:#FFCD56;
    border-color: #FFCD56;
    background-color: rgba(255,205,86,0.1);
  }
  .trolley:hover{
    color:#FFCD56;
    border-color: #FFCD56;
    background-color: rgba(255,205,86,0.1);
  }
  .trolley:focus{
    color:#FFCD56;
    border-color: #FFCD56;
    background-color: rgba(255,205,86,0.1);
  }
  .dia-btn{
    background-color: #EBB563;
    border-color: #EBB563;
  }
  .el-dropdown-link {
    cursor: pointer;
    color: #409EFF;
  }
  .el-icon-arrow-down {
    font-size: 12px;
  }
</style>



