export default {
  welcome (username) {
    const date = new Date()
    const hour = date.getHours()
    let time = hour < 6 ? '' : (hour <= 11 ? '' : (hour <= 13 ? '' : (hour <= 18 ? '' : '')))
    let welcomeArr = [
      '',
      'LOL',
      '',
      '',
      'Bug',
      '',
      '',
      '',
      '',
      '',
      '',
      ''
    ]
    let index = Math.floor((Math.random() * welcomeArr.length))
    return `${time}${username}${welcomeArr[index]}`
  },

  userString(str) {
    str = str.substr(1);
    str = str.substr(0, str.length - 1);
    return str
  },

  /**
   * data  name 
   * @param data
   * @param name String
   */
  formatList(data, name) {
    // 
    if(!name && typeof data !== Array && typeof name !== String){
      return
    }
    let obj = {}  // 
    let Arr = []  // 
    data.forEach(item => {
      !obj[item[name]] && (obj[item[name]] = [])
      obj[item[name]].push(item)
    })
    // pushArrr
    Object.values(obj).forEach(item => {
      Arr.push(item)
    })
    return Arr
  },

  filterByTimeAndName(orderList, time, name) {
    let obj = {}
    let Arr = []
    orderList.forEach(item => {
      !obj[item[time]] && (obj[item[time]] = [])
      obj[item[time]].push(item)
    })
    for (let attr in obj) {
      let arr = obj[attr]
      obj[attr].total = 0
      obj[attr].time = attr
      for (let x of arr) {
        obj[attr].total += x.total
        obj[attr].consumerName = x.consumerName
        obj[attr].shopName = x.shopName
        obj[attr].cid = x.cid
        obj[attr].sid = x.sid
      }
    }
    // pushArrr
    Object.values(obj).forEach(item => {
      Arr.push(item)
    })
    return Arr
  }
}



