<template>
  <el-container>
    <el-col :span="12" :offset="6">
      <comment :comments="comments" @add="addNewReplay" @remove="removeReply"></comment>
    </el-col>
  </el-container>
</template>

<script>
  import comment from '@/components/comment'

  export default {
    name: "ShopHomePage",
    components: {comment},
    data() {
      return {
        comments: [
          {
            id: 'comment0001', //id
            date: '2018-07-05 08:30',  //
            ownerId: 'talents100020', //id
            fromId: 'errhefe232213',  //id
            fromName: '',   //
            fromAvatar: 'http://ww4.sinaimg.cn/bmiddle/006DLFVFgy1ft0j2pddjuj30v90uvagf.jpg', //
            likeNum: 3, //
            content: '',  //
            stars: '',
            show: false,
            reply: [  //
              {
                id: '34523244545',  //id
                commentId: 'comment0001',  //idid
                fromId: 'observer223432',  //id
                fromName: '',  //
                fromAvatar: 'https://wx4.sinaimg.cn/mw690/69e273f8gy1ft1541dmb7j215o0qv7wh.jpg', //
                toId: 'errhefe232213',  //id
                toName: '',  //
                toAvatar: 'http://ww4.sinaimg.cn/bmiddle/006DLFVFgy1ft0j2pddjuj30v90uvagf.jpg',  //
                content: '',  //
                date: '2018-07-05 08:35',   //
                replyRole: ''
              }
            ]
          }
        ],
      }
    },
    created() {
      // 
      this.$get('/shop/getOrderHistory', {
        SID: this.$db.get('USER_ID')
      }).then(res => {
        console.log(res.data)
      }).catch(err => console.log(err))

      // 
      this.comments = []
      // 1 
      this.$get('/commentary/getInfoBySid', {
        Sid: this.$db.get('USER_ID')
      }).then(res => {
        let allComments = res.data.data
        for (let i = 0, len = allComments.length; i < len; i++) {
          this.comments.push({
            id: allComments[i].id,
            date: allComments[i].time,
            ownerId: allComments[i].id,
            fromId: allComments[i].cid,
            fromName: allComments[i].cname,
            fromAvatar: 'http://ww4.sinaimg.cn/bmiddle/006DLFVFgy1ft0j2pddjuj30v90uvagf.jpg',
            likeNum: 3,
            content: allComments[i].content,
            reply: [], // 
            stars: allComments[i].stars, // 
            show: false // 
          })
          // 2 
          let nowCoId = allComments[i].id // id
          // 2.1 
          this.$get('/shopReply/getInfoByCoid', {
            coid: nowCoId
          }).then(res => {
            let allShopReply = res.data.data
            for (let j = 0, len1 = allShopReply.length; j < len1; j++) {
              this.comments[i].reply.push({
                id: String(allShopReply[j].coid) + String(allShopReply[j].sid),  //id
                commentId: allComments[i].id,  //idid
                fromId: allShopReply[j].sid,  //id
                fromName: allShopReply[j].sname + "()",  //
                fromAvatar: 'https://wx4.sinaimg.cn/mw690/69e273f8gy1ft1541dmb7j215o0qv7wh.jpg', //
                toId: allComments[i].cid,  //id
                toName: allComments[i].cname,  //
                toAvatar: 'http://ww4.sinaimg.cn/bmiddle/006DLFVFgy1ft0j2pddjuj30v90uvagf.jpg',  //
                content: allShopReply[j].content,  //
                date: allShopReply[j].time ,  //
                replyRole: 'shop'
              })
            }
            // 2.2 
            this.$get('/consumerReply/getInfoByCoid', {
              coid: nowCoId
            }).then(res => {
              let allConsumerReply = res.data.data
              for (let j = 0, len1 = allConsumerReply.length; j < len1; j++) {
                this.comments[i].reply.push({
                  id: String(allConsumerReply[j].coid) + String(allConsumerReply[j].cid),  //id
                  commentId: allComments[i].id,  //idid
                  fromId: allConsumerReply[j].cid,  //id
                  fromName: allConsumerReply[j].cname,  //
                  fromAvatar: 'https://wx4.sinaimg.cn/mw690/69e273f8gy1ft1541dmb7j215o0qv7wh.jpg', //
                  toId: allComments[i].cid,  //id
                  toName: allComments[i].cname,  //
                  toAvatar: 'http://ww4.sinaimg.cn/bmiddle/006DLFVFgy1ft0j2pddjuj30v90uvagf.jpg',  //
                  content: allConsumerReply[j].content,  //
                  date: allConsumerReply[j].time,   //
                  replyRole: 'consumer'
                })
              }
            }).catch(err => console.log(err))
          }).catch(err => console.log(err))
        }
      }).catch(err => console.log(err))
      //  end

    },// created end
    methods: {
      /**
       * idx
       * @param idx
       * @param comment
       */
      addNewReplay(idx, comment) {
        this.comments[idx].reply.splice(0, 0, comment)
      },
      removeReply(idx, ridx) {
        this.comments[idx].reply.splice(ridx, 1)
      }
    }
  }
</script>

<style scoped>

</style>



