<template>
  <el-row>
    <el-col :span="4" :offset="4">
      <el-button class="button-new-tag" size="small" @click="addNewMenu"></el-button>
    </el-col>

<!--    main-->
    <el-col :span="14" :offset="5">
      <el-table
        :data="goodsList.filter(data => !search || data.name.toLowerCase().includes(search.toLowerCase()))"
        style="width: 100%">
        <el-table-column type="expand">
          <template slot-scope="props">
            <el-form label-position="left" inline class="demo-table-expand">
              <el-form-item label=" ID"><span>{{ props.row.id }}</span></el-form-item>
              <el-form-item label=""><span>{{ props.row.praise === 0 ? '' : props.row.praise }}</span></el-form-item>
              <el-form-item label=""><span>{{ props.row.name }}</span></el-form-item>
              <el-form-item label=""><span>{{ props.row.price }}</span></el-form-item>
              <el-form-item label=""><span>{{ props.row.stock }}</span></el-form-item>
              <el-form-item label=""><span>{{ props.row.type === null ? '' : props.row.type }}</span></el-form-item>
              <el-form-item label=""><span>{{ props.row.desc }}</span></el-form-item>
              <!--  -->
              <el-form-item label="">
                <div class="block">
                  <!--  -->
                  <el-image v-if="props.row.pictureUrl === '' || props.row.pictureUrl === null"
                    style="width: 100px; height: 100px" :src="require('@/assets/img/default.jpg')" fit="fill">
                  </el-image>
                  <!--  -->
                  <el-image v-else style="width: 100px; height: 100px" :src="props.row.pictureUrl" fit="fill"></el-image>
                </div>
              </el-form-item>
            </el-form>
          </template>
        </el-table-column>
        <el-table-column label="" prop="name"></el-table-column>
        <el-table-column label="" prop="price" sortable></el-table-column>
        <el-table-column label="" prop="stock" sortable></el-table-column>
        <el-table-column label="" prop="type" :filter-method="filterHandler"
                         :filters="[{text: '', value: ''}, {text: '', value: ''}, {text: '', value: ''}]">
        </el-table-column>

<!--        -->
        <el-table-column align="right">
          <template slot="header" slot-scope="scope">
            <el-input v-model="search" size="mini" placeholder=""/>
          </template>
          <template slot-scope="scope">
            <el-button size="mini" class="button-new-tag" @click="handleEdit(scope.$index, scope.row)"></el-button>
            <el-button size="mini" type="danger" @click="handleDelete(scope.$index, scope.row)"></el-button>
          </template>
        </el-table-column>

      </el-table>

<!--       dialog-->
      <el-dialog title="" :visible.sync="dialogFormVisible" :before-close="handleClose" center>
        <el-form :model="form">
          <el-form-item label="" :label-width="formLabelWidth">
            <el-input v-model="form.name" autocomplete="off"></el-input>
          </el-form-item>
          <el-form-item label="" :label-width="formLabelWidth">
            <el-input v-model="form.price" autocomplete="off"></el-input>
          </el-form-item>
          <el-form-item label="" :label-width="formLabelWidth">
            <el-input v-model="form.stock" autocomplete="off"></el-input>
          </el-form-item>
          <el-form-item label="" :label-width="formLabelWidth">
            <el-select v-model="form.type" placeholder="">
              <el-option label="" value=""></el-option>
              <el-option label="" value=""></el-option>
              <el-option label="" value=""></el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="" :label-width="formLabelWidth">
            <el-input v-model="form.desc" autocomplete="off"></el-input>
          </el-form-item>
          <el-form-item label="" :label-width="formLabelWidth">
            <!-- TODO:  -->
            <UploadPicture
              v-if="dialogFormVisible"
              :upUrl="form.url"
              @getUrl="setFormUrl">
            </UploadPicture>
          </el-form-item>
        </el-form>
        <div slot="footer" class="dialog-footer">
          <el-button @click="dialogFormVisible = false" class="reset-btn"> </el-button>
          <el-button type="warning" @click="submitChange" class="login-btn"> </el-button>
        </div>
      </el-dialog>
    </el-col>
  </el-row>
</template>

<script>
  import UploadPicture from '@/components/UploadPicture'
  import pictureUrl from '@/utils/pirctureUrl'

  export default {
    name: "MenuEdit",
    components: {
      UploadPicture
    },
    data() {
      return {
        goodsList: [],
        search: '',
        dialogFormVisible: false,
        form: {
          name: '',
          price: '',
          stock: '',
          type: '',
          desc: '',
          url: ''
        },
        formLabelWidth: '70px',

        addOpt: true,

        nowEditGid: Number,
        nowEditIndex: Number
      }
    },
    created() {
      this.$get('/shop/getAllGoodsBySid', {
        SID: this.$db.get('USER_ID')
      }).then(res => {
        this.goodsList = res.data.data
      }).catch(err => {
        console.log(err)
      })
    },
    methods: {
      handleEdit(index, row) {
        // GID
        this.nowEditIndex = index
        this.nowEditGid = row.id
        //
        this.addOpt = false
        this.form = {
          name: row.name,
          price: row.price,
          stock: row.stock,
          type: row.type,
          desc: row.desc,
          url: row.pictureUrl
        }
        this.dialogFormVisible = true
      },
      /**
       * 
       * @param index
       * @param row
       */
      handleDelete(index, row) {
        // console.log(row)
        this.$confirm('?', '', {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
        }).then(() => {
          this.$post('/shop/deleteGoodsByGid', {
            GID: row.id
          }).then(() => {
            this.$message({
              type: 'info',
              message: ''
            })
            // goodsList
            this.goodsList = this.goodsList.filter((x) => x.id !== row.id)
          })
        }).catch(() => {
          this.$message({
            type: 'info',
            message: ''
          })
        })
      },
      filterHandler(value, row, column) {
        const property = column['property'];
        return row[property] === value;
      },
      handleClose(done) {
        this.$confirm('')
          .then(_ => {
            done();
          })
          .catch(_ => {});
      },

      /**
       * 
       * addOpt = true => 
       * addOpt = false => 
       */
      submitChange() {
        this.dialogFormVisible = false
        // TODO: 
        if (this.addOpt) { // 
          console.log(this.form)
          this.$post('/shop/addGoods', {
            SID: Number(this.$db.get('USER_ID')),
            name: String(this.form.name),
            price: Number(this.form.price),
            stock: Number(this.form.stock),
            type: String(this.form.type),
            desc: this.form.desc,
            url: this.form.url
          }).then(res => {
            this.goodsList.push(res.data.data)
            this.$message({
              type: 'info',
              message: ''
            })
          }).catch(err => {
            this.$message({
              type: 'error',
              message: ''
            })
          })
        } else { // 
          this.$post('/shop/updateGoods', {
            GID: this.nowEditGid,
            name: this.form.name,
            price: this.form.price,
            stock: this.form.stock,
            type: this.form.type,
            desc: this.form.desc,
            url: this.form.url
          }).then(res => {
            this.$set(this.goodsList, this.nowEditIndex, res.data.data)
            this.$message({
              type: 'info',
              message: ''
            })
          }).catch(err => {
            this.$message({
              type: 'error',
              message: ''
            })
          })
        }
      },

      addNewMenu() {
        // 
        this.addOpt = true
        this.form = {
          name: '',
          price: '',
          stock: '',
          type: '',
          desc: '',
          url: ''
        }

        this.dialogFormVisible = true
      },

      /**
       * url
       * @param u
       */
      setFormUrl(u) {
        this.form.url = u
        // console.log(this.form)
      }
    }
  }
</script>

<style scoped>
  .demo-table-expand {
    font-size: 0;
  }
  .demo-table-expand label {
    width: 90px;
    color: #99a9bf;
  }
  .demo-table-expand .el-form-item {
    margin-right: 0;
    margin-bottom: 0;
    width: 50%;
  }
  .button-new-tag {
    margin-left: 10px;
    height: 32px;
    line-height: 30px;
    padding-top: 0;
    padding-bottom: 0;
  }
  .button-new-tag:hover{
    color:#FFCD56;
    border-color: #FFCD56;
    background-color: rgba(255,205,86,0.1);
  }
  .button-new-tag:focus{
    color:#FFCD56;
    border-color: #FFCD56;
    background-color: rgba(255,205,86,0.1);
  }
  .login-btn {
    margin: 10px 30px 10px 30px;
  }
  .login-btn:hover{
    background: rgba(230,162,60,0.7);
  }
  .reset-btn{
    margin:10px 30px 10px 30px;
  }
  .reset-btn:hover{
    background: rgba(255,255,255,0.5);
    color:#000;
  }
</style>



