<template>
  <el-row style="margin-top: 50px">
    <el-col :span="10" :offset="6">
      <el-form :model="ruleForm" :rules="rules" ref="ruleForm" label-width="100px" class="demo-ruleForm">
        <el-form-item label="" prop="name">
          <el-input v-model="ruleForm.name" placeholder=""></el-input>
        </el-form-item>
        <el-form-item label="" prop="telephone">
          <el-input v-model="ruleForm.telephone" placeholder=""></el-input>
        </el-form-item>
        <el-form-item label="" prop="address">
          <el-input v-model="ruleForm.address" placeholder=""></el-input>
        </el-form-item>
        <el-form-item label="" prop="managerName">
          <el-input v-model="ruleForm.managerName" placeholder=""></el-input>
        </el-form-item>
        <el-form-item label="" prop="manageEmail">
          <el-input v-model="ruleForm.managerEmail" placeholder=""></el-input>
        </el-form-item>
        <el-form-item>
          <el-button type="warning" @click="submitForm('ruleForm')" class="login-btn"></el-button>
          <el-button @click="resetForm('ruleForm')" class="reset-btn"></el-button>
        </el-form-item>
      </el-form>
    </el-col>
  </el-row>
</template>

<script>
  import {Notification} from "element-ui"

  export default {
    name: "SSetting",
    data() {
      return {
        oldData: {},
        ruleForm: {
          name: String,
          telephone: String,
          address: String,
          managerName: String,
          managerEmail: String
        },
        rules: {
          name: [
            { required: true, message: '', trigger: 'blur' },
            { min: 1, max: 40, message: ' 1  40 ', trigger: 'blur' }
          ],
          telephone: [
            { required: true, message: '', trigger: 'blur' },
            { min: 11, max: 11, message: ' 11 ', trigger: 'blur' }
          ],
        }
      };
    },
    created() {
      this.oldData = JSON.parse(this.$db.get('USER_INFO'))
      this.dataReset()
    },
    methods: {
      submitForm(formName) {
        this.$refs[formName].validate((valid) => {
          if (valid) {
            // 
            this.$confirm('?', '', {
              confirmButtonText: '',
              cancelButtonText: '',
              type: 'warning'
            }).then(() => {
              this.$get('/shop/editInfo', {
                SID: this.$db.get('USER_ID'),
                name: this.ruleForm.name,
                telephone: this.ruleForm.telephone,
                address: this.ruleForm.address,
                managerName: this.ruleForm.managerName,
                managerEmail: this.ruleForm.managerEmail,
              }).then(res => {
                this.$db.save('USER_INFO', res.data.data)
                this.oldData = JSON.parse(this.$db.get('USER_INFO'))
                this.dataReset()
                // 
                this.$message({
                  type: 'success',
                  message: '!'
                });
              }).catch(err => {
                console.log(err)
              })
            }).catch(() => {
              this.$message({
                type: 'info',
                message: ''
              });
              this.dataReset()
            });
          } else {
            console.log('error submit!!');
            return false;
          }
        });
      },
      resetForm(formName) {
        this.$refs[formName].resetFields();
        this.dataReset()
      },
      dataReset() {
        this.ruleForm = {
          name: this.oldData.username,
          telephone: this.oldData.telephone,
          address: this.oldData.address,
          managerName: this.oldData.managerName,
          managerEmail: this.oldData.managerEmail
        }
      }
    }
  }
</script>

<style scoped>
  .login-btn {
    margin: 10px 30px 10px 30px;
  }
  .login-btn:hover{
    background: rgba(230,162,60,0.7);
  }
  .reset-btn{
    margin:10px 30px 10px 30px;
  }
  .reset-btn:hover{
    background: rgba(255,255,255,0.5);
    color:#000;
  }
</style>



