<template>
  <div class="food-img">
    <el-button type="primary" v-on:click="shop">   </el-button>
    <el-button type="primary" v-on:click="$router.push('/signin/consumer')">   </el-button>

    <el-dialog :visible.sync="dialogVisible" width="60%" >
      <p></p>
      <p></p>
      <p>1. 
        
        </p>
      <p>2. 
        </p>
      <p>3. </p>
      <p>4. 
        
        </p>
      <p>5. 
        
        </p>
      <p>  10105557 
        </p>
      <el-checkbox v-model="checked" ></el-checkbox>
      <el-button type="warning" size="small" class="read-btn" @click="read"> </el-button>
    </el-dialog>

  </div>
</template>

<script>
  export default {
    name: "SignIn",
    data(){
      return{
        checked:false,
        dialogVisible:false
      };
    },
    methods:{
      shop(){
        this.dialogVisible=true;
      },
      read(){
        if(this.checked === true){
          this.dialogVisible=false;
          this.$router.push('/signin/shop');
        }
        else{
          this.$message({
            message: '',
            center: true
          });
        }
      }
    }
  }
</script>

<style scoped>
  .food-img{
    background-image: url("../images/orange.jpg");
    background-repeat: no-repeat;
    background-size: 100% 100%;
    background-attachment: fixed;
  }

  .el-button--primary {
    color: #fff;
    background-color: #FFCD56;
    border-color: #FFCD56;
    font-weight: bolder;
    font-size: large;
    margin: 500px 100px 200px 100px;
  }

  .el-button--primary:hover{
    color: #FFCD56;
    background-color: #fff;
    border-color: #FFCD56;
  }

  .el-button--primary:focus{
    color: #FFCD56;
    background-color: #fff;
    border-color: #FFCD56;
  }
  /*
.shop{
  margin:500px 100px 100px 30px;
  background: rgba(236,228,207,0.5);
  color:#000;
}
.shop:hover{
  background:rgba(198,197,193,0.7);
}
  .user{
    margin:500px 30px 100px 100px;
    background: rgba(198,197,193,0.5);
    color:#000;
  }
  .user:hover{
    background:rgba(250,243,226,0.7);
  }
  */
.el-dialog p{
  text-align: left;
}
.read-btn{
  margin:30px;
}
</style>



