<template>
  <div class="food-img">
    <el-row class="t">?</el-row>
    <el-row class="t">?</el-row>
    <el-row class="t">?</el-row>
    <el-form ref="signInForm" :model="form" :rules="rules" label-width="80px" class="signIn-box">
      <h3 class="signIn-title">   </h3>
      <el-form-item label="" prop="username" class="form-item">
        <el-input type="text" prefix-icon="el-icon-user" placeholder="" clearable v-model="form.username"/>
      </el-form-item>
      <el-form-item label="" prop="password" class="form-item">
        <el-input type="password" prefix-icon="el-icon-lock" placeholder="" clearable v-model="form.password"/>
      </el-form-item>
      <el-form-item label="" prop="checkPassword" class="form-item">
        <el-input type="password" prefix-icon="el-icon-lock" placeholder="" clearable v-model="form.checkPassword"/>
      </el-form-item>
      <el-form-item label="" prop="phone" class="form-item">
        <el-input type="text" prefix-icon="el-icon-mobile" placeholder="" clearable v-model="form.phone" oninput = "value=value.replace(/[^\d]/g,'')" maxlength="11"/>
      </el-form-item>
      <el-button type="warning" class="sign-btn" v-on:click="onSubmit"> </el-button>
<!--      <el-button class="login-btn" v-on:click="$router.push('/login')"> </el-button>-->
    </el-form>
  </div>
</template>

<script>
  export default {
    name: "ConsumerSignIn",
    data() {
      return {
        form: {
          username: '',
          password: '',
          checkPassword:'',
          phone:''
        },

        //  el-form-item  prop 
        rules: {
          username: [
            {required: true, message: '', trigger: 'blur'},
            { min: 3, max: 25, message: ' 3  25 ', trigger: 'blur' }
          ],
          password: [
            {required: true, message: '', trigger: 'blur'},
            { min: 3, max: 30, message: ' 3  30 ', trigger: 'blur' }
          ],
          checkPassword:[
            {required: true, message: '', trigger: 'blur'},
            { min: 3, max: 30, message: ' 3  30 ', trigger: 'blur' }
          ],
          phone:[
            {required: false, message: '', trigger: 'blur'},
            { min: 11, max: 11, message: ' 11 ', trigger: 'blur' }
          ]
        }
      }
    },
    methods: {
      onSubmit() {
        const reg = 11 && /^((13|14|15|17|18)[0-9]\d{8})$/
        // 
        if (this.form.username === '') {
          this.$message({
            message: '',
            type:'error',
            center: true
          })
          return
        }
        if (this.form.password === '') {
          this.$message({
            message: '',
            type:'error',
            center: true
          })
          return
        }
        if(this.form.password !== this.form.checkPassword){
          this.$message({
            message:'',
            type:'error',
            center:true
          })
          return
        }
        if (this.form.phone === '') {
          this.$message({
            message:'',
            type:'error',
            center: true
          })
          return
        }
        if (!reg.test(this.form.phone)) {
          this.$message({
            message:'',
            type:'error',
            center:true
          })
          return
        }
        // FIXME: 
        this.$syspost('/signIn/consumer', {
          name: this.form.username,
          password: this.form.password,
          telephone: this.form.phone
        }).then(res => {
          let msg = res.data.msg
          // 
          if (msg === 'SUCCESS') {
            this.$confirm('?', '', {
              confirmButtonText: '',
              cancelButtonText: '',
              type: 'warning'
            }).then(() => {
              this.$router.push('/login')
            }).catch(() => {
              this.$router.push('/index')
            })
          } else {
            this.$message({
              message: msg,
              type:'error',
              center:true
            })
          }
        }).catch(err => console.log(err))
      }

    }
  }
</script>

<style scoped>
  .food-img{
    background-image: url("../images/orange.jpg");
    background-repeat: no-repeat;
    background-size: 100% 100%;
    background-attachment: fixed;
    height:100%;
  }
  .signIn-box {
    border: 1px solid #DCDFE6;
    width: 500px;
    margin: 0 auto 100px auto;
    background: rgba(255,255,255,0.7);
    padding: 20px 40px 20px 40px;
    border-radius: 5px;
    -webkit-border-radius: 5px;
    -moz-border-radius: 5px;
    box-shadow: 0 0 25px 	#DCDFE6;
  }
  .signIn-title {
    text-align: center;
    margin: 20px auto 40px auto;
    color: #E7B563;
  }
  .form-item{
    margin:10px 0 20px 0;
  }
  .sign-btn {
    margin: 10px 30px 10px 30px;
  }
  .sign-btn:hover{
    background: rgba(230,162,60,0.7);
  }
  .login-btn{
    margin:10px 30px 10px 30px;
  }
  .login-btn:hover{
    background: rgba(255,255,255,0.5);
    color:#000;
  }
  .t{
    color:rgba(0,0,0,0);
  }
</style>



