<template>
  <div >
    <el-steps :active="active" finish-status="success" dialogVisible="dialogVisible" align-center class="step">
      <el-step title="" description=""></el-step>
      <el-step title="" description="1-3"></el-step>
      <el-step title="" description=""></el-step>
      <el-step title="" description=""></el-step>
    </el-steps>

    <el-form :model="applyForm" :rules="rules" ref="ruleForm" label-width="150px" class="shop">
      <el-form-item label="" prop="name">
        <el-input v-model="applyForm.name" clearable></el-input>
      </el-form-item>

      <el-form-item label="" prop="shopkeeper">
        <el-input v-model="applyForm.shopkeeper" clearable></el-input>
      </el-form-item>

      <el-form-item label="" prop="phone">
        <el-input type="text" prefix-icon="el-icon-mobile" placeholder=""
                  clearable v-model="applyForm.phone" oninput = "value=value.replace(/[^\d]/g,'')" maxlength="11"/>
      </el-form-item>

      <el-form-item label="" prop="address">
        <el-input v-model="applyForm.address" clearable></el-input>
      </el-form-item>

      <el-form-item label="">
        <el-upload
          action="https://jsonplaceholder.typicode.com/posts/"
          list-type="picture-card"
          :limit="1"
          :on-preview="handlePictureCardPreview"
          :on-remove="handleRemove">
          <i class="el-icon-plus"></i>
        </el-upload>
      </el-form-item>

      <el-form-item label="" prop="pass">
        <el-input type="password" v-model="applyForm.pass" autocomplete="off"></el-input>
      </el-form-item>
      <el-form-item label="" prop="checkPass">
        <el-input type="password" v-model="applyForm.checkPass" autocomplete="off"></el-input>
      </el-form-item>

      <el-form-item label="">
        <el-upload
          action="https://jsonplaceholder.typicode.com/posts/"
          list-type="picture-card"
          :on-preview="handlePictureCardPreview"
          :on-remove="handleRemove">
          <i class="el-icon-plus"></i>
        </el-upload>
      </el-form-item>

      <el-form-item>
        <el-button type="warning" class="btn" @click="next"></el-button>
      </el-form-item>

    </el-form>
  </div>
</template>

<script>
  import selectAddress from '@/components/Address'
  export default {
    name: "ShopSignIn",
    components:{
      selectAddress
    },
    data() {
      const validatePass = (rule, value, callback) => {
        if (value === '') {
          callback(new Error(''));
        } else {
          if (this.applyForm.checkPass !== '') {
            this.$refs.applyForm.validateField('checkPass');
          }
          callback();
        }
      }
      const validatePass2 = (rule, value, callback) => {
        if (value === '') {
          callback(new Error(''));
        } else if (value !== this.applyForm.pass) {
          callback(new Error('!'));
        } else {
          callback();
        }
      }
      return {
        active:0,
        dialogImageUrl: '',
        dialogVisible: true,

        applyForm: {
          name: '',
          shopkeeper: '',
          phone: '',
          address: '',
          options:food,
          value: '',
          imageUrl: '',
          license:'',
          registration:'',
          representative:'',
          manageLocation:'',
          pass: '',
          checkPass: ''
        },
        rules: {
          name: [
            { required: true, message: '', trigger: 'blur' },
            { min: 2, max: 16, message: ' 2 16 ', trigger: 'blur' }
          ],
          shopkeeper: [
            { required: true, message: '', trigger: 'change' }
          ],
          phone: [
            { required: true, message: '', trigger: 'blur' },
            { min: 11, max: 11, message: ' 11 ', trigger: 'blur' }
          ],
          address: [
            { required: true, message: '', trigger: 'change' }
          ],
          pass: [
            { required: true, message: '', trigger: 'blur' },
            { min: 3, max: 30, message: ' 3  30 ', trigger: 'blur' }
          ],
          checkPass: [
            { required: true, message: '', trigger: 'blur' },
            { min: 3, max: 30, message: ' 3  30 ', trigger: 'blur' }
          ]
        }
    };
  },
  methods: {
    handleRemove(file, fileList) {
      console.log(file, fileList);
    },
    handlePictureCardPreview(file) {
      this.dialogImageUrl = file.url;
      this.dialogVisible = true;
    },
    handleChange(value) {
      console.log(value);
    },
    next: function () {
      const reg = 11 && /^((13|14|15|17|18)[0-9]\d{8})$/;
      if (this.applyForm.name === '') {
        this.$message({
          message: '',
          type: 'error',
          center: true
        });
        return
      }
      if (this.applyForm.shopkeeper === '') {
        this.$message({
          message: '',
          type: 'error',
          center: true
        });
        return
      }
      if (this.applyForm.phone === '') {
        this.$message({
          message: '',
          type: 'error',
          center: true
        });
        return
      }
      if (!reg.test(this.applyForm.phone)) {
        this.$message({
          message: '',
          type: 'error',
          center: true
        });
        return
      }
      if (this.applyForm.pass !== this.applyForm.checkPass) {
        this.$message({
          message: '',
          type: 'error',
          center: true
        });
        return
      }

      // FIXME: 
      this.$syspost('/signIn/shop', {
        name: this.applyForm.name,
        managerName: this.applyForm.shopkeeper,
        telephone: this.applyForm.phone,
        address: this.applyForm.address,
        password: this.applyForm.pass
      }).then(res => {
        let msg = res.data.msg
        // 
        if (msg === 'SUCCESS') {
          this.active = 4
          // 
          this.$confirm('?', '', {
            confirmButtonText: '',
            cancelButtonText: '',
            type: 'warning'
          }).then(() => {
            this.$router.push('/login')
          }).catch(() => {
            this.$router.push('/index')
          })
        } else {
          this.$message({
            message: msg,
            type: 'error',
            center: true
          })
        }
      }).then(err => console.log(err))

      // if (this.active++ > 2) {
      //   this.active = 0;
      //   //this.dialogVisible = false;
      // }
    }
  }
  }

  const food = [
    {
    "value": "",
    "label": "",
    "children": [{
      "value": "",
      "label": "",
      "children": [
        {"value": "", "label": ""}, {"value": "", "label": ""},
        {"value": "/","label": "/"}, {"value": "//", "label": "//"},
        {"value": "/", "label": "/"}, {"value": "/", "label": "/"},
        {"value": "//", "label": "//"}, {"value": "", "label": ""},
        {"value": "", "label": ""}, {"value": "", "label": ""},
        {"value": "/", "label": "/"}, {"value": "", "label": ""},
        {"value": "", "label": ""}, {"value": "//", "label": "//"},
        {"value": "", "label": ""}, {"value": "", "label": ""}]
    }]
  }];


</script>

<style scoped>
  .step{
    margin:100px 0 100px 0;
  }

  .btn{
    margin: 10px 50px 100px 50px;
  }

  .shop{
    width:500px;
    margin:auto;
    align:center;
  }

</style>



