<template>
  <el-table
    :data="dataInTable()"
    style="width: 100%"
    height="500"
    :row-class-name="tableRowClassName"
    :span-method="objectSpanMethod">
    <el-table-column
      sortable
      prop="time"
      label=""
      width="180">
    </el-table-column>
    <el-table-column
      prop="goodsName"
      label=""
      width="180">
      <template slot-scope="scope">
        <el-popover
          placement="top-start"
          width="200"
          trigger="hover">
          <el-image :src="scope.row.picture"></el-image>
          <el-button slot="reference" style="border: 0px">{{scope.row.goodsName}}</el-button>
        </el-popover>
      </template>
    </el-table-column>
    <el-table-column
      sortable
      prop="num"
      label="">
    </el-table-column>
    <el-table-column
      prop="consumerName"
      label="">
    </el-table-column>
    <el-table-column
      sortable
      prop="total"
      label="">
    </el-table-column>
    <el-table-column
      prop="state"
      label=""
      :filters="[{ text: '', value: '' }, { text: '', value: '' },{ text: '', value: '' }, { text: '', value: '' }]"
      :filter-method="filterTag"
      filter-placement="bottom-end">
      <template slot-scope="scope">
        <el-tag
          :type="tagType(scope.row)"
          disable-transitions>{{scope.row.state}}</el-tag>
      </template>
    </el-table-column>

    <!--        -->
    <el-table-column
      align="right">
      <template slot="header" slot-scope="scope">
        <el-input
          v-model="search"
          size="mini"
          placeholder=""/>
      </template>
    </el-table-column>
  </el-table>
</template>

<script>
  export default {
    name: "SHistory",
    data() {
      return {
        history_list: [

      //     cid: 1
      // consumerName: "Zhang San"
      // gid: 3
      // goodsName: ""
      // id: 4
      // num: 0
      // picture: null
      // price: 200
      // shopName: "mai"
      // sid: 1
      // state: ""
      // time: "02:03:21"
      // total: 100
        ],

        search: '',

        spanArr: [],
        pos: 1
      }
    },
    created() {
      this.$get('/shop/getOrderHistory', {
        SID: this.$db.get('USER_ID')
      }).then(res => {
        this.history_list = res.data.data
        // 
        this.getSpanArr(res.data.data)
      }).catch(err => console.log(err))
    },
    methods: {
      filterTag(value, row) {
        return row.state === value;
      },

      dataInTable() {
        return this.history_list.filter(data => !this.search ||
          data.goodsName.toLowerCase().includes(this.search.toLowerCase()) ||
          data.consumerName.toLowerCase().includes(this.search.toLowerCase()))
      },
      tagType(row) {
        switch (row.state) {
          case '' : return 'primary'
          case '' : return 'danger'
          case '' : return 'warning'
          case '' : return 'info'
        }
      },
      tableRowClassName({row, rowIndex}) {
        // let set = this.dateSet
        // if (set.indexOf(row.time) % 2 === 0) {
        //   return 'warning-row';
        // } else {
        //   return 'success-row';
        // }
      },

      // span test 
      // 
      getSpanArr (data) {
        console.log(data)//
        this.spanArr = []
        for (let i = 0; i < data.length; i++) {
          if (i === 0) {
            // 0
            this.spanArr.push(1)
            this.pos = 0
          } else {
            if (data[i].time === data[i - 1].time && data[i].consumerName === data[i - 1].consumerName) {
              // timepush 0
              this.spanArr[this.pos] += 1
              this.spanArr.push(0)
            } else {
              // push 1
              this.spanArr.push(1)
              this.pos = i
            }
          }
        }
        console.log(this.spanArr)
      },
      // 
      objectSpanMethod ({ row, column, rowIndex, columnIndex }) {
        // columnIndex === 0 
        if (columnIndex === 0 || columnIndex === 3) {
          const _row = this.spanArr[rowIndex]
          const _col = _row > 0 ? 1 : 0
          return {
            rowspan: _row,
            colspan: _col
          }
        }
      }
    },
    computed: {
      dateSet() {
        let res = new Set();
        let list = this.dataInTable()
        for (let i = 0, len = list.length; i < len; i++) {
          res.add(list[i].time)
        }
        return Array.from(res)
      }
    }
  }
</script>

<style scoped>

</style>



